export default function NavReducer(state="Login",action)
{
    switch(action.type)
    {
        case "Register":
            return "Register"
        case "Login":
            return "Login"
        case "Home":
            return "Home"
        case "Find a Pet":
            return "Find a Pet"
        case "Logout":
            return "Logout"
        case "Dogs":
            return "Dogs"            
        case "Cats":
            return "Cats"
        case "Fishes":
            return "Fishes"
        case "Birds":
            return "Birds"
        case "Accessories":
            return "Accessories"
        case "Donate":
            return "Donate"
        case "About Us":
            return "About Us"
        case "Error":
            return "Error"
        default:
            return "Home"
    }    
}