import axios from 'axios';
import bglogin from './images/bg-login.jpg';

function Login({store}){
  function handleSubmit(){
    alert(document.getElementsByName("pwd")[0].value)
    axios.post('http://localhost:8081/check',{
      un: document.getElementById("idun").value,
      pw: document.getElementsByName("pwd")[0].value
    }).then((res) =>{
      console.log(res.data)
      if(res.data.message != "Incorrect Password !"||"Username not Matched !"){
        store.dispatch({"type":"login","data":{"un":res.data.message.name, "role":res.data.message.Role,"token":res.data.token}})
      }
    })
  }
  function handlemover(){
    document.getElementById("lp")["style"]["boxShadow"]="5px 5px 10px grey"
  }
  function handlemleave(){
    document.getElementById("lp")["style"]["boxShadow"]="0px 0px 0px black"
  }
  return(
    <div className="bg-login p-5">
      <center>
        <div onMouseLeave={handlemleave}  onMouseOver={handlemover} id="lp" className="login-page">
          <p><b><u style={{textShadow:"1px 2px 5px #1976D2 -1px -2px 5px black",fontFamily:"Bree Serif",fontSize:"3vh",color:'white'}}>Login Page</u></b></p>
            Username: <input className='bdrs boxclr' type="text" id="idun"/><br/>
            Password  : <input className='bdrs boxclr' type="Password" name="pwd"/><br/><br/>
          <button style={{width:"6vw",height:"4vh",fontSize:"2vh",borderRadius:"5px",border:"2px solid white",backgroundColor:"transparent",color:"white",cursor:'pointer'}} onClick={handleSubmit}>Login</button>
        </div>
      </center>
    </div>
  );
}

export default Login;