import dog from './images/dog.png';
import cat from './images/cat.png';
import fish from './images/fish.png';
import birds from './images/bird.png';


export default function Findapet({store}){
    function handleDog(){
        store.dispatch({"type":"Dogs"})
    }
    function handleCat(){
        store.dispatch({"type":"Cats"})
    }
    function handleFish(){
        store.dispatch({"type":"Fishes"})
    }
    function handleBird(){
        store.dispatch({"type":"Birds"})
    }
    function handleDonate(){
        store.dispatch({"type":"Donate"})
    }
    function handleback(){
        store.dispatch({"type":"Home"})
    }
    return(
        <div className="fluid-container bg-con2">
            <div className="row">
                {/* <video loop autoPlay muted id="bgvideo">
                    <source src={backgroundvideo} type="video/mp4" />
                </video> */}
                <div className="d-flex col-12 bg-light">
                    <div className="d-flex flex-row justify-content-start col-4 m-2">
                        <button className='Main-btn m-2' onClick={handleback}>Back</button>
                    </div>
                    <h1 className="Main-head1 col-4 text-center">Find A Pet</h1>
                    <div className="d-flex flex-row justify-content-end col-4 ml-auto">
                        <button className='Main-btn float m-2' onClick={handleDonate}>Register a Pet</button>
                    </div>
                </div>
                {/* <div className="cntr dropcon col-12">
                    <label for="cars">Choose a Pet: </label>
                    <select className="drop" name="Pet" id="Pet">
                        <option defaultValue="Select">Select</option>
                        <option value="Dog">Dog</option>
                        <option value="Cat">Cat</option>
                        <option value="Fish">Fish</option>
                        <option value="Birds">Birds</option>
                    </select>
                </div> */}
                <div className="col-12 col-md-3">
                    <div onClick={handleDog} className="m-3 p-3 crd text-center shadow">
                        <img className="img2" src={dog}/>
                        <h1 className="min-p1">Dogs</h1>
                    </div>
                </div>
                <div onClick={handleCat} className="col-12 col-md-3">
                    <div className="m-3 p-3 crd text-center shadow">
                        <img className="img2" src={cat}/>
                        <h1 className="min-p1">Cats</h1>
                    </div>
                </div>
                <div onClick={handleFish} className="col-12 col-md-3">
                    <div className="m-3 p-3 crd text-center shadow">
                        <img className="img2" src={fish}/>
                        <h1 className="min-p1">Fishes</h1>
                    </div>
                </div>
                <div onClick={handleBird} className="col-12 col-md-3">
                    <div className="m-3 p-3 crd text-center shadow">
                        <img className="img2" src={birds}/>
                        <h1 className="min-p1">Birds</h1>
                    </div>
                </div>
                <br/>
                <div className='col-12 text-center'>
                    
                </div>
            </div>
        </div>
    );
}