import axios from 'axios';
import { useState, useEffect } from 'react';

function DogsAd({store}) {
    function handleback(){
        store.dispatch({"type":"Find a Pet"})
    }
    const [r, setR] = useState(null);

    useEffect(() => {
        if (r === null) {
            axios.get('http://localhost:8081/Dogs', {
                headers: {
                    "authorization": `Bearer ${localStorage.getItem("token")}`,
                    "Accept": "application/json",
                    "Content-Type": "application/json"
                }
            })
            .then((res) => {
                console.log(res.data);
                setR(res.data);
            });
        }
    }, [r]);

    function handleDelete(event){
        alert(event.currentTarget.getAttribute("ref1"))
        axios.delete('http://localhost:8081/deletedog',{
            params:{
                Name:event.currentTarget.getAttribute("ref1")
            }
            //to provide data to the axois to delete
        }).then((res)=>{
            console.log(res.data)
        })
    }

    if (r !== null) {
        return (
            <div className='container-fluid'>
                <div className='row'>
                    <div className="d-flex col-12 bg-light">
                        <div className="d-flex flex-row justify-content-start col-4 m-2">
                            <button className='Main-btn mt-1' onClick={handleback}>Back</button>
                        </div>
                        <h1 className="Main-head1 col-4 text-center">Dogs</h1>
                    </div>
                    {r.map((dogs) => (
                        <div key={dogs.Name} className='col-4'>
                            <div className='card text-center shadow p-3'>
                                <h5>Name: {dogs.Name}</h5>
                                <h5>Pet Type: {dogs.PetType}</h5>
                                <h5>Gender: {dogs.Gender}</h5>
                                <h5>Age: {dogs.Age}</h5>
                                <h5>Owner: {dogs.Owner}</h5>
                                <h5>City: {dogs.City}</h5>
                                <h5>Contact: {dogs.Contact}</h5>
                                <div className='text-center'>
                                    <button className='Main-btn' onClick={handleDelete} ref1={dogs.Name}>Delete</button>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        );
    } else {
        return (
            <div className='text-center'>
                Data is fetching!
            </div>
        );
    }
}

export default DogsAd;
