import './App.css';
import Appbar from './components/Appbar.js';
import Navbar from './components/Navbar.js';
import Login from './components/Login.js'
import Reg from './components/Register.js';
import Home from './components/Home.js';
import Findapet from './components/Findapet.js';
import Dogs from './components/Dogs.js';
import Accessories from './components/Accessories.js';
import Donate from './components/Donate.js';
import Aboutus from './components/Aboutus.js';
import Error from './components/Error.js';
import Cats from './components/Cats.js';
import Fishes from './components/Fishes.js';
import Birds from './components/Birds.js';
import DogsAd from './components/DogAd.js';
import CatsAd from './components/CatsAd.js';
import FishesAd from './components/FishesAd.js';
import BirdsAd from './components/BirdsAd.js';



export default function App({store}) {
  function Page(){ /*react router log*/ 
    switch(store.getState().NavReducer){
      case "Login":
        return (<div><Login store={store}/></div>)
      case "Register":
        return (<div><Reg store={store}/></div>)
      case "Home":
        return (<div><Home store={store}/></div>)
      case "Find a Pet":
        if (localStorage.getItem("Role") == 2 || localStorage.getItem("Role") == 1 || localStorage.getItem("Role") == 3)
          return (<div><Findapet store={store}/></div>)
        else
          return(<div><Login /></div>)
      case "Dogs":
        if (localStorage.getItem("Role") == 2)
          return (<div><Dogs store={store}/></div>)
        else if(localStorage.getItem("Role")==3 || localStorage.getItem("Role") == 1)
          return(<div><DogsAd store={store}/></div>)
        else
          return(<div><Error /></div>)
      case "Cats":
        if (localStorage.getItem("Role") == 2)
          return (<div><Cats store={store}/></div>)
        else if(localStorage.getItem("Role")==3 || localStorage.getItem("Role") == 1 )
          return(<div><CatsAd store={store}/></div>)
        else
          return(<div><Error /></div>)
      case "Fishes":
        if (localStorage.getItem("Role") == 2)
          return (<div><Fishes store={store}/></div>)
        else if(localStorage.getItem("Role")==3 || localStorage.getItem("Role") == 1)
          return(<div><FishesAd store={store}/></div>)
        else
          return(<div><Error /></div>)
      case "Birds":
        if (localStorage.getItem("Role") == 2)
          return (<div><Birds store={store}/></div>)
        else if(localStorage.getItem("Role")==3 || localStorage.getItem("Role") == 1)
          return(<div><BirdsAd store={store}/></div>)
        else
          return(<div><Error /></div>)
      case "Accessories":
        if (localStorage.getItem("Role") == 2 || localStorage.getItem("Role") == 1 || localStorage.getItem("Role") == 3)
          return (<div><Accessories store={store}/></div>)
        else
          return(<div><Login /></div>)  
      case "Donate":
      if (localStorage.getItem("Role") == 3 || localStorage.getItem("Role") == 1)
        return (<div><Donate store={store}/></div>)
      else
        return(<div><Error /></div>)
      case "About Us":
        return (<div><Aboutus store={store}/></div>)
      default:
        return (<div><Home /></div>)
    }
  }
  return (
    <div className="App">
        <div>
          <Navbar store={store} />
          <Page />
        </div>
    </div>
  );
}


