import search from './images/search.gif';
import time from './images/time.gif';
import adopt from './images/adopt.png';
import Findapet from './Findapet.js';
import { Link } from 'react-router-dom';

export default function Home({store}){
    function handlemover(){
        document.getElementById("lp")["style"]["boxShadow"]="5px 5px 10px grey"
    }
    function handlemleave(){
        document.getElementById("lp")["style"]["boxShadow"]="0px 0px 0px black"
    }
    function handleAdopt(){
        store.dispatch({"type":"Find a Pet"})
    }
    return(
        <div className='fluid-container'>
            <div className='row'>
                <div className="col-12 d-md-none">
                    <div className="bg-home w-100"  onMouseLeave={handlemleave}  onMouseOver={handlemover}  id="lp">
                        <h1 className="Main-head">Adopt a Pet</h1>
                        <button className="Main-btn" onClick={handleAdopt}  onMouseLeave={handlemleave}  onMouseOver={handlemover}>Adopt Now</button>
                        {/* <Link to="/Find"><h3>Find a Pet</h3></Link> */}
                    </div>
                </div>
                <div className=" col-12 d-none d-md-block">
                    <div className="bg-home w-100 p-5"  onMouseLeave={handlemleave}  onMouseOver={handlemover}  id="lp">
                        <h1 className="Main-head">Adopt a Pet</h1>
                        <button className="Main-btn" onClick={handleAdopt}  onMouseLeave={handlemleave}  onMouseOver={handlemover}>Adopt Now</button>
                    </div>
                </div>
                <div className='col-12'>
                    <h1 className="min-h2">Re-Home and Adopt a Pet in India</h1>
                    <h2 className="min-p1">Every Pet Deserves a Good Home. #Adoptlove</h2>
                </div>
                <div className="bg-home1 boot col-12">
                    <br/>
                    <div style={{width:"50%"}}>
                        <img style={{width:"100%"}}  alt="" src="https://dm6g3jbka53hp.cloudfront.net/static-images/adopt-me-pet-02032021.jpg"/>
                    </div>
                    <div className="p-5 m-3" style={{width:"50%",border:"20px solid #00758F",borderRadius:"10px"}}  onMouseLeave={handlemleave}  onMouseOver={handlemover}>
                        <h1 className="min-p2" style={{textDecoration:"underline"}}>Adoption Procedure</h1>
                        <br/>
                        <div className="boot" style={{display:"flex"}}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="69" height="69" fill="currentColor" class="bi bi-search" viewBox="0 0 16 16">
                            <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001q.044.06.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1 1 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0"/>
                        </svg>
                            <p className="min-h1">Search Pet</p>
                        </div>
                        <p style={{textAlign:"center",fontFamily:"cursive",fontSize:"1.3vw",color:"grey"}}>For Searching Pets Enter the Type of Pet you Needed in the Blank</p>
                        <div className="boot" style={{display:"flex"}}>
                            <img width="69" height="69" src={time} alt="search"/>
                            <p className="min-h1">Connect</p>
                        </div>
                        <p style={{textAlign:"center",fontFamily:"cursive",fontSize:"1.3vw",color:"grey"}}>By using connect you can contact with owner and know about your pet. and get ready for the adoption.</p>
                        <div className="boot" style={{display:"flex"}}>
                            <img width="69" height="69" src={adopt} alt="search"/>
                            <p className="min-h1">Adopt</p>
                        </div>
                        <p style={{textAlign:"center",fontFamily:"cursive",fontSize:"1.3vw",color:"grey"}}>By filling details in form of adoption you can take your pet legally.</p>
                    </div>
                </div>
            </div>
        </div>
    );
}