import backgroundvideo from './images/bg-video.mp4';
export default function Aboutus(){
    return(
        <div className="container-fluid bg-ab">
            <div className="row ab-txt">
                <video loop autoPlay muted id="bgvideo">
                    <source src={backgroundvideo} type="video/mp4" />
                </video>
                <div className="col-12 bg-white">
                    <h1 className="Main-head1 text-center">About us</h1>
                </div>
                <div className="col-6 p-3">
                    <h4>Welcome to Pet Adoption and Accessories Management System</h4>
                    <p className='ab-head'> Our platform is more than just a pet adoption website  it's a community built on compassion, care, and commitment to animal welfare.</p>
                </div>
                <div className="col-6 p-3">
                    <h4>Our Mission</h4>
                    <p className='ab-head'>our mission is simple: to connect homeless pets with loving families and individuals who are ready to provide them with forever homes. We believe that every animal deserves a second chance, and we're here to make that happen.</p>
                </div>
                <div className="col-6 p-3">
                    <h4>What Sets Us Apart</h4>
                    <p className='ab-head'>Our comprehensive platform offers a range of features designed to support every stage of the adoption journey. From personalized matchmaking algorithms to educational resources and post-adoption support, we're here to ensure that every pet finds their perfect match and every adopter feels confident and prepared for their new furry family member.</p>
                </div>
                <div className="col-12 p-3">
                    <h4>Our Values</h4>
                    <ul className='ab-head'>
                        <li>Compassion: We believe in treating every animal with kindness, respect, and empathy.</li>
                        <li>Transparency: We are committed to honesty and integrity in all our interactions, from adoption listings to financial transparency.</li>
                        <li>Collaboration: We recognize the importance of working together with shelters, rescues, and adopters to create positive outcomes for animals.</li>
                        <li>Education: We strive to provide valuable resources and information to help both pets and their owners thrive.</li>
                        <li>Impact: We measure our success not only by the number of adoptions but by the lives we touch and the communities we strengthen.</li>
                    </ul>
                </div>
                <div className="col-6">
                    <h4>Our Team</h4>
                    <ul className='ab-head'>
                        <li>K.Siva Karthik </li> 
                        <li>M.Surya Theja </li>  
                        <li>G.Navadeep </li>     
                    </ul>
                </div>
            </div>
        </div>
    );
}