import axios from 'axios';
import { useState, useEffect } from 'react';

function Cats({store}) {
    function handleback(){
        store.dispatch({"type":"Find a Pet"})
    }
    const [r, setR] = useState(null);

    useEffect(() => {
        if (r === null) {
            axios.get('http://localhost:8081/Cats', {
                headers: {
                    "authorization": `Bearer ${localStorage.getItem("token")}`,
                    "Accept": "application/json",
                    "Content-Type": "application/json"
                }
            })
            .then((res) => {
                console.log(res.data);
                setR(res.data);
            });
        }
    }, [r]);

    function handleDelete(event) {
        alert(event.currentTarget.getAttribute("ref1"));
        axios.delete('http://localhost:8081/delete', {
            params: {
                name: event.currentTarget.getAttribute("ref1")
            }
        })
        .then((res) => {
            console.log(res.data);
            // Update the state or perform any necessary actions after deletion
        });
    }

    if (r !== null) {
        return (
            <div className='container-fluid'>
                <div className='row'>
                    <div className="d-flex col-12 bg-light">
                        <div className="d-flex flex-row justify-content-start col-4 m-2">
                            <button className='Main-btn mt-1' onClick={handleback}>Back</button>
                        </div>
                        <h1 className="Main-head1 col-4 text-center">Cats</h1>
                    </div>
                    {r.map((cats) => (
                        <div key={cats.Name} className='col-4'>
                            <div className='card  text-center shadow p-3'>
                                <h5>Name: {cats.Name}</h5>
                                <h5>Pet Type: {cats.PetType}</h5>
                                <h5>Gender: {cats.Gender}</h5>
                                <h5>Age: {cats.Age}</h5>
                                <h5>Owner: {cats.Owner}</h5>
                                <h5>City: {cats.City}</h5>
                                <h5>Contact: {cats.Contact}</h5>
                                {/* <button onClick={handleDelete} ref1={dogs.Name}>Delete</button> */}
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        );
    } else {
        return (
            <div className='text-center'>
                Data is fetching!
            </div>
        );
    }
}

export default Cats;
