import axios from "axios";

export default function Donate({ store }) {
    function handleback() {
        store.dispatch({ "type": "Find a Pet" });
    }

    function handleform() {
        const petData = {
            Name: document.getElementById("name").value,
            Type: document.getElementById("type").value,
            Gender: document.querySelector('input[name="gender"]:checked').value,
            Age: document.getElementById("age").value,
            Owner: document.getElementById("oname").value,
            City: document.getElementById("city").value,
            Contact: document.getElementById("contact").value,
        };

        axios.post('http://localhost:8081/Form', petData)
            .then((res) => {
                console.log(res.data);
            })
            .catch((error) => {
                console.error('Error submitting pet data:', error);
            });
    }

    return (
        <div className="container-fluid form-con">
            <div className="row">
                <div className="d-flex col-12 bg-light">
                    <div className="d-flex flex-row justify-content-start col-4 m-2">
                        <button className='Main-btn mt-1' onClick={handleback}>Back</button>
                    </div>
                    <h1 className="Main-head1 col-4 text-center">Register a Pet</h1>
                </div>
                <div className="col-12 p-5">
                    <center>
                        <div className="bg-form1 p-5">
                            <div className="d-flex">
                                <div className="p-3">
                                    <label htmlFor="name">Pet Name: </label>
                                    <input id="name" type="text" />
                                </div>
                                <div className="p-3">
                                    <label htmlFor="type">Pet Type: </label>
                                    <select id="type" defaultValue="" required>
                                        <option value="" disabled>Select Pet Type</option>
                                        <option value="Dog">Dog</option>
                                        <option value="Cat">Cat</option>
                                        <option value="Fish">Fish</option>
                                        <option value="Bird">Bird</option>
                                    </select>
                                </div>
                            </div>
                            <div className="d-flex">
                                <div className="p-3 d-flex flex-row justify-content-center">
                                    <h5>Gender: </h5>
                                    <div className="p-1">
                                        <input type="radio" id="male" name="gender" value="male" />
                                        <label htmlFor="male">Male:</label>
                                        <input type="radio" id="female" name="gender" value="female" />
                                        <label htmlFor="female">Female: </label>
                                    </div>
                                </div>
                                <div className="p-3">
                                    <label htmlFor="age">Age :</label>
                                    <input id="age" type="number" />
                                </div>
                            </div>
                            {/* Remaining form fields */}
                            <div className="p-3">
                                <label htmlFor="oname">Owner Name: </label>
                                <input id="oname" type="text" name="oname" />
                            </div>
                            <div className="p-3">
                                <label htmlFor="city">City: </label>
                                <input id="city" type="text" name="city" />
                            </div>
                            <div className="p-3">
                                <label htmlFor="contact">Contact: </label>
                                <input id="contact" type="number" />
                            </div>
                            <div className="p-3">
                                <button className="Main-btn" onClick={handleform}>Submit</button>
                            </div>
                        </div>
                    </center>
                </div>
            </div>
        </div>
    );
}
